body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
   
}

.container {
    width: 80%;
    margin: auto;
    overflow: hidden;
}

.content {
    padding: 20px;
    background: #fff;
    margin-top: 20px;
}

.content h2 {
    color: #333;
}

.content p {
    line-height: 1.6;
}

.content ul {
    list-style-type: none;
    padding: 0;
}

.content ul li {
    margin: 10px 0;
}
