
body {
    background: #ffffff;
}

.brand {
    color: #FF9138;
}



.border-hover:hover {
    border-top: 5px solid #ff9138;
    transform: translateY(-4px);
}



.navbar-toggler {
    border: 3px solid #FF9138;
}


.navbar-toggler:focus {
    box-shadow: none;
}

#nav-length {
    width: 90%;
}

@media screen and (max-width : 992px) {
    #nav-length {
        width: 100%;
    }

    .border-hover {
        border-top: 0;
    }

    .border-hover:hover {
        border-top: 0;
    }

    #sign-in {
        border-radius: 50px;
    }
}

/* body section */
.icon-height {
    height: 28px;
    width: 28px;
}

.icon-container {
    background: #FF9138;
}

.dark-background {
    background: #ffffff;
}
#socialWrap {
    border-radius: 0px 24px 24px 0px;
}
.active:focus{
    background-color: #FF9138;
}