input[type=file]::file-selector-button {
    margin-right: 20px;
    border: none;
    background: #ffffff;
    padding: 10px 20px;
    border-radius: 10px;
    color: #000000;
    cursor: pointer;
    transition: background .2s ease-in-out;
  }
  
input[type=file]::file-selector-button:active {
    background: #ff9138;
  }
.halfimage{
  width: 25vw;
}
.decodeimage{
  margin-left: 10vw;
}