/* inputBox */
.inputBox {
    position: relative;
    width: 80wh;
  }
  
  /* inputBox input */
  .inputBox input {
    width: 100%;
    padding: 10px;
    border: 1px solid black;
    background: #ffffff; /* Fixed the background color property */
    border-radius: 5px;
    outline: none;
    color: #000000;
    font-size: 1em;
    transition: 0.5s;
  }

  /* Initial styling for the label span */
.inputBox span {
    position: absolute;
    left: 0;
    padding: 10px;
    pointer-events: none;
    font-size: 1em;
    color: rgba(19, 1, 1, 0.25);
    text-transform: uppercase;
    transition: 0.5s;
}

/* Styling for the label when input is valid or focused */
.inputBox input:valid ~ span,
.inputBox input:focus ~ span {
    color: #ff9138;
    transform: translateX(10px) translateY(-7px);
    font-size: 0.65em;
    padding: 0 10px;
    border-left: 1px solid #ff9138;
    border-right: 1px solid #ff9138;
    letter-spacing: 0.2em;
}

/* inputBox:nth-child(2) input:valid ~ span, inputBox:nth-child(2) input:focus ~ span */
.inputBox input:valid ~ span,
.inputBox input:focus ~ span {
  background: #ff9138;
  color: #ffffff;
  border-radius: 2px;
}




  /* inputBox input:valid, inputBox input:focus */
.inputBox input:valid,
.inputBox input:focus {
  border: 1px solid #ff9138; /* Fixed the color property */
}
